<template>
  <div>
    <div>
      <div id="registerAll">
        <div id="registerTitle">
          <p ></p>
        </div>
        <form action="">
          <!--  :  , true  -->
          <div class="lines">
            <!--      ,   .-->
            <!--     true ,    .    .-->
            <input id="registerIDInput" v-model="signup.id" type="text" maxlength="20" placeholder=" ">
            <button class="Btn" v-on:click='showModal = true'></button>
          </div>
          
          <div class="lines">
            <input class="passwordInput" type="password" v-model="signup.password1" placeholder=" ">
          </div>
          <div class="lines">
            <input class="passwordInput" type="password" v-model="signup.password2" placeholder=" " @blur="passwordJudgement">
            <!--blur   ! ==> method passwordJudgement() -->
            <div class=notices>
              <div class="notice" v-if="!passwordJudgementTry"> </div>
              <div class="notice" v-else-if="!passwordCheck">  </div>
              <div class="notice" v-else> </div>
            </div>
          </div>
            
          <div class="lines">
            <input id="registerPhone" v-model="signup.contact" type="text" onKeyup="inputPhoneNumber(this);" maxlength="13">
            <button class="Btn" v-on:click='showCheckPhModal = true'> </button>
          </div>
          <p id="registerConfirm" v-on:click="registerSubmit"></p>
        </form>
      </div>
      <modal v-if="showModal" v-on:close="showModal = false && emailSubmit">
        <h2 slot="header"> </h2>
      </modal>
      <modal v-if="showCheckPhModal" v-on:close="showCheckPhModal = false">
        <h2 slot="header"> </h2>
        <!-- <p slot="body" @click="emailSubmit"></p> -->
      </modal>
    </div>
  </div>
</template>

<script>
import Modal from './common/Modal.vue';
import axios from 'axios';

export default {
  components: {
    Modal,
  },
    data: function () { 
      return { 
        signup: { 
          id: null, 
          password1: null,
          password2: null,
          contact: null,
          // passwordFinal: passwordJudgement(), ->  v-model input data     
        },
        passwordJudgementTry: false,
        passwordCheck: true, //           .
        showModal: false,
        showCheckPhModal: false
        };
    },
  methods: {
    passwordJudgement() {
      this.passwordJudgementTry = true
      if (this.signup.password1 === this.signup.password2){
        console.log("true")
        this.passwordCheck = true //     
        // return true; ->      passwordFinal: passwordJudgement()  return   
        //                   duplication error .          .
      }
      else{
        this.passwordCheck = false
        // return false;
      }
    },
    registerSubmit() {
            
        try {
          axios
            .post("http://18.117.182.57:3000/auth/joinMember", {
            // body: {
            //   email: this.login.userEmail,
            //   password: this.login.userPassword,
            // },
            email: this.signup.id,
            password: this.signup.password1,
            contact: this.signup.contact,
            })
            .then((res) => {
            if (res.status === 200) {
              // console.log(this.$cookies.get("connect.sid"));
            //   console.log(this.cookies.keys().join("\n"));
                console.log(" !");
                this.clearInput();
            }
            });
        } catch (error) {
            console.log(error);
        }
    },
    emailSubmit(){
      try{
        axios
        .post("http://18.117.182.57:3000/auth/sendSMS", {
          contact: this.signup.contact,
        })
        .then((res) => {
          if (res.ok === true){
            console.log(res.data.message);
          }
        });
      }catch (error) {
      console.log(error);
    }
    } 
  }
}


</script>

<style scoped>
/*  */
@font-face {
  font-family: "roboto-regular";
  src: url("../assets/fonts/Roboto-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "elice-regular";
  src: url("../assets/fonts/EliceDigitalBaeum_Regular.ttf") format("truetype");
}

:root {
  /*    */
  --color-ivory: #faf3e0;
  --color-beige: #eabf9f;
  --color-brown: #b68973;
  --color-dark: #1e212d;
  --color-green: #9eb998;
  --color-khakii: #67917d;

  /*   */
  --font-size-mega: 40px;
  --font-size-large: 35px;
  --font-size-medium: 25px;
  --font-size-regular: 20px;
  --font-size-small: 15px;
  --font-size-micro: 10px;
}

/* CSS */

#registerAll{
    position: absolute;
    font-family: "roboto-regular";
    left: 50%;
    top: 15em;
    transform: translate(-50%, -30%);
    text-align: center;
    margin:0 0 100px 0;
}

#registerTitle{
    font-weight: bold;
    font-size: 35px;
    text-decoration: underline;
    color: #1e212d;
    margin: 40px;
}

