    width: 50%;
}

input{
    display: inline-block;
    border: none;
    border-bottom: solid 2px var(--color-brown);
    padding-left: 10px;
    padding-right: 60px;
    font-size: var(--font-size-small);
    
}

input:focus{
    outline: none;
}

.Btn {
    display: inline-block;
    border-radius: 20px;
    background: none;
    border-color: var(color-brown);
    font-family: "elice-regular";
    font-size: var(font-size-small);
    font-weight: bold;
    color: #1e212d;
}

