  padding: 15px 30px;
  font-weight: bold;
  width: 70%;
}

.passwordInput{
  width: 70%;
}

#registerConfirm{
    font-family: 'roboto';
    font-size: var(font-size-large);
    color: #1e212d;
    margin:80px 10px;
    font-weight: bold;
}

#registerPhone{
    width: 43%;

}

</style>